import { createRouter, createWebHashHistory } from 'vue-router'
import Glory from '../components/mobile/glory/Glory.vue'
import Introduce from '../components/mobile/introduce/Introduce.vue'
import mMain from '../components/mobile/mMain/mMain.vue'
import Join from '../components/mobile/join/Join.vue'
const routes = [
  {
    path: '/',
    component: mMain
  },
  {
    path: '/glory',
    component: Glory
  },
  {
    path: '/introduce',
    component: Introduce
  },
  {
    path: '/join',
    component: Join
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
