import Back from '../back/Back.vue';
export default {
  name: 'mHeader',

  data() {
    return {
      headerName: '杭电大学生创新科技孵化器',
      kind: 'home'
    };
  },

  components: {
    Back
  },
  watch: {
    $route: {
      handler: function (val, oldVal) {
        if (val.fullPath.indexOf('glory') !== -1) {
          this.headerName = '实验室荣誉';
          this.kind = 'other';
        } else if (val.fullPath.indexOf('introduce') !== -1) {
          this.headerName = '实验室简介';
          this.kind = 'other';
        } else if (val.fullPath.indexOf('join') !== -1) {
          this.headerName = '加入实验室';
          this.kind = 'other';
        } else {
          this.headerName = '杭电大学生创新科技孵化器';
          this.kind = 'home';
        }
      },
      deep: true,
      immediate: true
    }
  }
};