let body = document.querySelector('body');
export default {
  name: 'Header',

  data() {
    return {};
  },

  methods: {
    fixHeader() {
      let header = document.querySelector('.header-container');
      let top = document.documentElement.scrollTop || document.body.scrollTop; // console.log(top)

      if (top >= 300) {
        header.classList.remove('default-header');
        header.classList.add('fixed-header');
      } else {
        header.classList.remove('fixed-header');
        header.classList.add('default-header');
      }
    },

    lightTags() {
      let tags = document.querySelectorAll('.tag-item');
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      let itemTop = document.querySelectorAll('.item');

      if (top <= 255) {
        this.changeClass(tags, 0);
      } else if (top <= itemTop[1].offsetTop) {
        this.changeClass(tags, 1);
      } else if (top <= itemTop[2].offsetTop) {
        this.changeClass(tags, 2);
      } else {
        this.changeClass(tags, 3);
      }
    },

    changeClass(a, n) {
      for (let i = 0; i < a.length; i++) {
        a[i].classList.remove('bold');
        a[i].classList.add('thin');
      }

      a[n].classList.remove('thin');
      a[n].classList.add('bold');
    },

    toPlace(val) {
      let contentTop = document.querySelectorAll('.content');

      if (val === 0) {
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;
      } else {
        document.documentElement.scrollTop = contentTop[val - 1].offsetTop - 100;
        document.body.scrollTop = contentTop[val - 1].offsetTop - 100;
      }
    }

  },

  mounted() {
    window.onscroll = () => {
      this.fixHeader();
      this.lightTags();
    };
  }

};